import React from 'react'
import styled from 'react-emotion'
import ScaleLoader from 'react-spinners/ScaleLoader'

const MARGIN = '80px'

const Loading = styled('div')`
  margin-top: ${MARGIN};
  text-align: center;
  font-size: 30px;
`

const Wrapper = styled('div')`
  margin-top: ${MARGIN};
`

const Table = styled('table')`
  width: 500px;
  padding: 0;
  table-layout: auto;
  background-color: transparent;
  border-spacing: 0;
  border-collapse: collapse;
  border-style: hidden;
  border-radius: 5px;

  & thead th {
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    opacity: 0.5;
    padding: 10 20px;
    border-bottom: 1px dotted white;
  }

  & tbody td {
    padding: 12px 20px;
    line-height: 2;
    font-weight: 200;
  }

  & tbody > tr {
    display: table-row;
    border-top: white;
  }
`

export const Licences = ({ isLoading, error, data }) => {
  if (isLoading) {
    return (
      <Loading>
        <ScaleLoader size={120} color="white" loading />
      </Loading>
    )
  }

  return (
    <Wrapper>
      <Table>
        <thead>
          <tr>
            <th>name</th>
            <th>licence</th>
            <th />
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>some name</td>
            <td>other thing</td>
            <td>mtf</td>
          </tr>
        </tbody>
      </Table>
    </Wrapper>
  )
}



